import { Component, ElementRef, AfterViewInit } from '@angular/core';
import { DraftJs } from '@lebaotrung/react_draft-js';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'draft-js',
  templateUrl: './draft-js.component.html',
  styleUrls: ['./draft-js.component.css']
})
export class DraftJsComponent implements AfterViewInit {
  private el: ElementRef<any>;
  constructor(el: ElementRef) {
    this.el = el;
  }

  ngAfterViewInit(): void {
    const options = {};
    // tslint:disable-next-line:no-unused-expression
    new DraftJs(this.el.nativeElement, options);
  }
}
